import React from 'react'
import Price from './Price'
import {connect} from 'react-redux'
import CryptoSpark from './CryptoSpark'

const PriceList = ({ prices }) => {
  let total = 0;
  let priceIndex = 0;

  prices.map( price => {
    total += price.value;
    priceIndex = (total/350.0)-1;
  } )

  if (priceIndex > 0) {
    var priceIndexSpan = <span className="success">
      (+{priceIndex}%)
    </span>
  } else {
    var priceIndexSpan = <span className="fail">
      (){priceIndex}%)
    </span>
  }

  return (
  <ul>
    { prices.map( price =>
      <div>
        <Price
          key={price.id}
          {...price}
        />
        <CryptoSpark code={price.code} />
        <hr />
      </div>
    )}
    <li>TOTAL: <strong>{total}</strong> {priceIndexSpan}</li>
  </ul>
)}

const mapStateToProps = (state) => ({
  prices: state.prices
})

const mapDispatchToProps = {
  //onTodoClick: toggleTodo
}

const VisiblePriceList = connect(
  mapStateToProps,
  mapDispatchToProps
)(PriceList)

export default VisiblePriceList



